body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}
.disabled{
    pointer-events: none;
    opacity: 0.4;
}
.gold{
    fill: gold !important;
}

.loader{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    height: 100vh;
}
.pokemon-list{
    padding: 10px;
    text-align: 'center';
}

.custom-avatar{
    height: 100px !important;
    width: 100px  !important;
    margin: 0 auto;
    background: #ececec;
}


